<template>
	<div class="container">
		<div class="title-container">
			<div class="push-title">
				<span>推荐歌单</span>
				<h2>为你精挑细选</h2>
			</div>
			<div class="more mdui-ripple">查看更多</div>
		</div>
		<div class="recommend content">
			<scroll :data="list3" :observer="true" :freeMode="true">
				<div class="swiper-slide">
					<div v-for="item in list1" class="img" @click="selectItem(item)">
						<div class="mdui-ripple ripple"><img :src="item.picUrl"></div>
						<p>{{item.name}}</p>
					</div>
				</div>
				<div class="swiper-slide" style="margin-left: 5px;">
					<div v-for="item in list2" class="img mdui-ripple" @click="selectItem(item)">
						<div class="mdui-ripple ripple"><img :src="item.picUrl"></div>
						<p>{{item.name}}</p>
					</div>
				</div>
			</scroll>
		</div>
	</div>
</template>

<script type="text/javascript">
	import {recommend} from '@/common/js/axios.js' 
	import scroll from '@/base/swiper'

	export default {
		data() {
			return {
				list1: [],
				list2: [],
				list3: null
			}
		},
		methods: {
			_initlist() {
				setTimeout(() => {
				recommend()
				.then(res => {
					for(let i=0;i<3;i++) {
						this.list1.push(res.data.result[i])
						this.list2.push(res.data.result[i+3])
					}
					this.list3 = this.list1.concat(this.list2)
				}).catch(err => {
					this.$mdui.alert(err)
				})		
				}, 20)
			},
			selectItem(item) {
				this.$router.push(`sheet/${item.id}`)
			}
		},
		created() {
			this._initlist()
		},
		components: {
			scroll
		}
	}
</script>

<style type="text/css" scoped>
	.container {
		position: relative;
		width: 100%;
	}
	.title-container {
		position: relative;
		top: 0;
		width: 92%;
		margin-left: 4%;
		margin-top: 25px;
	}
	.title-container >span{
		color: rgba(0,0,0,.2);
		font-size: 10px;
	}
	.more {
		position: absolute;
		right: 0;
		width: 50px;
		padding: 4px 6px;
		font-size: 12px;
		display: flex;
		border:1px solid rgba(0,0,0,.4);
		border-radius: 20px;
		justify-content: center;
		align-items: center;
		bottom: 0;
	}
	.push-title h2{
		margin: 0;
		margin-top: 2px;
	}
	.recommend {
		position: relative;
		width: 92%;
		margin-left: 4%;
		margin-top: 20px;

	}
	.recommend img {
		position: relative;
		width: 108px;
		height: 108px;
		border-radius: 5px;
	}
	.img {
		display: inline-block;
		height: 138.15px;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	.swiper-slide {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}
	.img p {
		width: 108px;
		font-size: 12px;
		margin: 0;
		margin-top: -1px;
	}
	.ripple {
		overflow: hidden;
	}
</style>