import axios from 'axios'

//从list中点击调出player组件，对song进行封装
class Song {
	constructor({name, singer, img, id, url}) {
		this.name = name
		this.singer = singer
		this.img = img
		this.id = id
		this.url = url
	}
}

export default function createSong(song) {
	const id = song.id
	const url = `https://autumnfish.cn/song/detail?ids=${id}`
	let musicUrl = null
	return new Promise((reslove, reject) => {
		getUrl(id).then(result => {
				musicUrl = result
			axios.get(url).then(res => {
				let song = res.data.songs[0]
				console.log(musicUrl)
				const ret = new Song({
					name: song.name,
					singer: song.ar[0].name,
					img: song.al.picUrl,
					id: song.al.id,
					url: musicUrl
				})
				reslove(ret)
			}).catch(err => {
				reject(err)
			})
		}).catch(err => {
			musicUrl = err
		})
		
	})
}

function getUrl(id) {
	const url = `https://autumnfish.cn/song/url?id=${id}`
	return new Promise((reslove, reject) => {
		axios.get(url)
		.then(res => {
			reslove(res.data.data[0].url)
		}).catch(err => {
			reject(err)
		})
	})
} 