<template>
	<div class="container" v-show="fullScreen">
		<div class="header">
			<div class="header-icon">
				<div class="i back mdui-ripple" @click="back()">
					<i class="i mdui-icon material-icons">&#xe5c4;</i>
				</div>
				<div class="info">
					<h3 class="i title">{{currentSong && currentSong.name}}</h3>
					<p class="singer">{{currentSong && currentSong.singer}}</p>
				</div>
				<div class="i more mdui-ripple">
					<i class="i mdui-icon material-icons">&#xe80d;</i>
				</div>
			</div>
		</div>
		<div class="cd">
			<img :src="currentSong.img">
		</div>
		<div class="bottom">
			<div class="bottom-icon i-top">
				<i class="mdui-icon material-icons">&#xe87e;</i>
				<i class="mdui-icon material-icons">&#xe87e;</i>
				<i class="mdui-icon material-icons">&#xe87e;</i>
				<i class="mdui-icon material-icons">&#xe87e;</i>
				<i class="mdui-icon material-icons">&#xe87e;</i>
			</div>
			<div class="bottom-icon i-bottom">
				<i class="mdui-icon mdui-ripple material-icons mode">&#xe87e;</i>
				<i class="mdui-icon mdui-ripple material-icons prev">&#xe045;</i>
				<i class="mdui-icon mdui-ripple material-icons play" v-text="playstate" @click="play"></i>
				<i class="mdui-icon mdui-ripple material-icons next">&#xe044;</i>
				<i class="mdui-icon mdui-ripple material-icons list">&#xe87e;</i>
			</div>
		</div>
		<div class="cover-img-blur" ref="blur"></div>
		<audio ref="audio" :src="currentSong.url"></audio>
	</div>
</template>

<script type="text/javascript">
	import {mapGetters, mapActions} from 'vuex'

	export default {
		computed: {
			...mapGetters([
				'fullScreen',
				'playlist',
				'currentSong',
				'playing',
			])
		},
		watch: {
			currentSong(song) {
				const img = song.img
				this.$refs.blur.style.backgroundImage = `url(${img})`
				//请求url的同时play会报错，用this.$nextTick()来解决
				this.$nextTick(() => {
					this.$refs.audio.play()
				})
			},
			playing(newPlaying) {
				const audio = this.$refs.audio
				if (newPlaying) {
					this.playstate = 'play_circle_outline'
					audio.play()
				} else {
					this.playstate = 'pause_circle_outline'
					audio.pause()
				}
			}
		},
		methods: {
			back() {
				this.togglePlayer()
			},
			play() {
				const audio = this.$refs.audio
				this.togglePlay()
			},
			...mapActions([
				'togglePlay',
				'togglePlayer'
			]),
		},
		data() {
			return {
				'playstate': 'play_circle_outline'
			}
		}
	}
</script>

<style type="text/css" scoped>
	.bottom {
		position: absolute;
		bottom: 0;
		width: 100%;
		z-index: 9999999999;
	}
	.i-top i{
		display: inline-block;
		font-size: 26px;
		color: white;
		font-weight: none;
	}
	.play {
		font-size: 56px;
	}
	.prev {
		font-size: 36px;
	}
	.next {
		font-size: 36px;
	}
	.i-bottom {
		position: relative;
		width: 92%;
		margin-left: 4%;
		display: flex;
		justify-content: space-between;
		height: 50px;
		color: white;
		align-items: center;
	}
	.i-top {
		position: relative;
		width: 92%;
		margin-left: 4%;
		display: flex;
		justify-content: space-between;
		height: 50px;
	}
	.container {
		position: fixed;
		z-index: 999999;
		width: 100%;
		height: 100%;
		background: black;
		top: 0;
	}
	.header {
		position: absolute;
		width: 100%;
		top:0;
		z-index: 99;
		height: 40px;
		padding: 5px 10px;
	}
	.cover-img-blur {
		position: absolute;
		width: 100%;
		height: 100%;
		filter: blur(35px);
		transform: scale(3);
		top: 0;
		background-position: 35% -10%;
		background-size: cover;
	}
	.cover-img-blur::after {
		content: "";
		position: relative;
		display: block;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,.5);
	}
	.header-icon {
		position: relative;
		width: 100%;
		display: flex;
		align-items: center;
	}
	.i {
		display: inline-block;
		color: white;
	}
	.back {
		position: relative;
		left: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		display: flex;
		align-items: center;
	}
	.back i {
		position: absolute;
		left: 0;
		font-size: 30px;
	}
	.more {
		position: absolute;
		right: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		display: flex;
		align-items: center;
	}
	.title {
		position: relative;
	}
	h3 {
		margin: 0;
	}
	.cd {
		position: absolute;
		top: 150px;
		width: 100%;
		display: flex;
		justify-content: center;
		z-index: 9999999999999999;
	}
	.cd img {
		width: 200px;
		height: 200px;
		border:4px solid rgba(255,255,255,.1);
		border-radius: 50%;
		animation: cd 20s linear;
		animation-play-state: paused;
	}
	.singer {
		font-size: 12px;
		color: rgba(255,255,255,.4);
		margin: 0;
	}
	@keyframes cd {
		from {transform: rotate(0deg);}
		to {transform: rotate(360deg);}
	}
</style>