<template>
	<div>
		<search @selectitem="selectItem"></search>
	</div>
</template>

<script type="text/javascript">
	import Search from '@/base/search'
	import createSong from '@/common/js/music/song.js'
	import {mapActions} from 'vuex'

	export default {
		components: {
			Search
		},
		methods: {
			selectItem(data) {
				createSong(data.item)
				.then((res) => {
					this.selectPlay({
						song: res,
						index: data.index,
						list: data.list
					})
				})
				
			},
			...mapActions([
				'selectPlay'
			])
		}
	}
</script>