<template>
  <div class="list-container" v-show="list">
	<div class="list-btn">
		<div class="play">
			<i class="mdui-icon material-icons">&#xe039;</i>
			<p>播放全部</p>
			<div class="collect mdui-ripple">+ 收藏 ({{list.collect}})</div>
		</div>
	</div>
		<ul class="mdui-list">
		  <li class="mdui-list-item mdui-ripple li" v-for="(item, index) in list.list" @click="selectItem(item, index, list)">
				<p class="order">{{index+1}}</p>
			  <div>
				<p class="name">{{item.name}}</p>
				<div class="info">
				  <p class="author">{{item.ar[0].name}} - {{item.al.name}}</p>
				</div>
			  </div>
		  </li>
		</ul>
   </div>
</template>

<script type="text/javascript">
	export default {
		props: {
			list: {
				type: Object,
				default: {}
			},
		},
		methods: {
			selectItem(item, index, list) {
				const data = {
					item: item,
					index: index,
					list: list
				}
				this.$emit('selectitem', data)
			}
		}
	}
</script>

<style type="text/css" scoped>
	.list-container {
		position: relative;
		width: 100%;
		background: white;
		border-radius: 20px;
		margin-top: -30px;
		z-index: 999;
	}
	.mdui-list li {
		padding: 0;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	.name {
		margin: 0;
	}
	.order {
		position: relative;
		font-size: 14px;
		color: rgba(0,0,0,.5);
		text-align: center;
		width: 45px;
		display: flex;
		justify-content: center;
	}
	.info p{
		font-size: 10px;
		color: rgba(0,0,0,.5);
		margin: 0;
		overflow:hidden; 
		text-overflow:ellipsis;
		space-white: nowrap;
	}
	.info {
		margin-top: 5px;
	}
	.play {
		padding: 6px 0;
		margin-left: 4%;
		width: 96%;
	}
	.play i {
		position: relative;
		font-size: 24px;
		padding-right: 15px;
		display: inline-block;
	}
	.play >p {
		display: inline-block;
	}
	.collect {
		width: 100px;
		height: 15px;
		padding: 10px;
		background: red;
		border-radius: 20px;
		color: white;
		display: inline-block;
		position: absolute;
		right: 10px;
		text-align: center;line-height: 15px;
	}
</style>