<template>
<div class="container">
	<div class="top" ref="header">
		<div class="header">
			<div class="header-icon">
				<div class="i back mdui-ripple" @click="back">
					<i class="i mdui-icon material-icons">&#xe5c4;</i>
				</div>
				<h3 class="i title">歌单</h3>
				<div class="i search mdui-ripple">
					<i class="mdui-icon material-icons">&#xe8b6;</i>
				</div>
				<div class="i more mdui-ripple">
					<i class="mdui-icon material-icons">&#xe5d4;</i>
				</div>
			</div>
		</div>
		<div class="cover">
			<img class="cover-img" :src="list.img">
			<div class="info">
				<div class="img-container"><img class="img" :src="list.img"></div>
				<div class="content">
					<h3 class="content-name">{{list.name}}</h3>
					<div class="user mdui-ripple">
						<img class="user-img" :src="list.creator.avatarUrl">
						<p>{{list.creator.nickname}} ></p>
					</div>
					<div class="description mdui-ripple">
						<p>{{list.description}}</p>
					</div>
				</div>
			</div>
		</div>
		<div class="bottom-icon">
			<div class="message b-icon mdui-ripple">
				<i class="mdui-icon material-icons">&#xe0c9;</i>
			</div>
			<div class="message b-icon mdui-ripple">
				<i class="mdui-icon material-icons">&#xe80d;</i>
			</div>
			<div class="message b-icon mdui-ripple">
				<i class="mdui-icon material-icons">&#xe2c4;</i>
			</div>
			<div class="message b-icon mdui-ripple">
				<i class="mdui-icon material-icons">&#xe5ca;</i>
			</div>
		</div>
	</div>
	<music-list :list="list" @selectitem="selectItem"></music-list>
</div>
</template>

<script type="text/javascript">
	import {sheet} from '@/common/js/axios.js'
	import createList from '@/common/js/music/list.js'
	import MusicList from '@/base/music_list'
	import createSong from '@/common/js/music/song.js'
	import {mapActions} from 'vuex'

	export default {
		created() {
			this._initlist(this.$route.params.id)
		},
		data() {
			return {
				list: [],
			}
		},
		methods: {
			_initlist(id) {
				let ret = []
				let _this = this
				sheet(id).then(res => {
					ret = createList(res.data.playlist)
					_this.list = ret
				})
				.catch(err => {
					this.$mdui.alert('获取歌单数据失败')
				})
			},
			selectItem(data) {
				const item = data.item
				const index = data.index
				const list = data.list.list
				createSong(item).then(res => {
					console.log(res)
					this.selectPlay({
						song: res,
						index: index,
						list: list
					})
				})
				
			},
			back() {
				this.$router.go(-1)
			},
			...mapActions([
				'selectPlay'
			])
		},
		watch: {
			list(val) {
				this.list = val
			}
		},
		components: {
			MusicList
		}
	}
</script>

<style type="text/css" scoped>
	.container {
		position: relative;
		width: 100%;
		overflow: hidden;
	}
	.top {
		position: relative;	
	}
	.header {
		position: fixed;
		width: 100%;
		height: 40px;
		padding: 5px 0;
		z-index: 9999;
		background-position: top;
		background-size: 100% 40px;
	}
	.header-icon {
		position: relative;
		width: 92%;
		margin-left: 4%;
		display: flex;
		align-items: center;
	}
	.i {
		display: inline-block;
		color: white;
	}
	.search {
		position: absolute;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		right: 50px;
		display: flex;
		align-items: center;
	}
	.back {
		position: relative;
		left: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		display: flex;
		align-items: center;
	}
	.back i {
		position: absolute;
		left: 0;
		font-size: 30px;
	}
	.more {
		position: absolute;
		right: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		display: flex;
		align-items: center;
	}
	.title {
		position: relative;
	}
	h3 {
		margin: 0;
	}
	.cover {
		position: relative;
		top: 0;
		width: 100%;
	}
	.cover-img{
		position: relative;
		width: 100%;
		height: 300px;
		z-index: 1;
		object-fit: bottom;
		transform: scale(2);
		filter: blur(50px);
		background: rgba(0,0,0,.25);
	}
	.info {
		position: absolute;
		top: 60px;
		z-index: 999;
		width: 92%;
		margin-left: 4%;
	}
	.img {
		width: 120px;
		height: 120px;
		border-radius: 5px;
		display: inline-block;
		background: #eee;
	}
	.img-container {
		position: relative;
		display: inline-block;
		left: 0;
	}
	.content {
		position: absolute;
		margin-left: 15px;
		display: inline-block;
	}
	.content-name {
		color: white;
		font-weight: bold;
		margin: 0;
		font-size: 18px;
		line-height: 24px;
	}
	.user-img {
		position: relative;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		margin-top: 5px;
		display: inline-block;
	}
	.user {
		display: flex;
		align-items: center;
	}
	.user >p {
		position: relative;
		display: inline-block;
		margin-left: 5px;
		font-size: 14px;
		color: rgba(255,255,255,.6);
	}
	.description {
		position: relative;
		bottom: 0;
		height: 26px;
		overflow: hidden;
		text-overflow: ellipsis;
		margin-top: 5px;
	}
	.description >p {
		margin: 0;
		font-size: 12px;
		color: rgba(255,255,255,.6);
		text-overflow: ellipsis;
	}
	.bottom-icon {
		position: absolute;
		bottom: 30px;
		z-index: 9;
		padding-bottom: 15px;
		display: flex;
		justify-content: space-between;
		width: 92%;
		margin-left: 4%;
	}
	.b-icon {
		display: inline-block;
	}
	.b-icon i {
		font-size: 28px;
		color: white;
	}
</style>