import axios from 'axios'

export function slide() {
	const url = 'https://autumnfish.cn/banner?type=1'
	return new Promise((reslove, reject) => {
		axios.get(url)
		.then(function(res) {
			reslove(res)
		})
		.catch(function(err) {
			reject(err)
		})
	})
}

