<template>
<div class="swiper-container">
    <div class="swiper-wrapper">
        <slot></slot>
    </div>
</div>
</template>

<script type="text/javascript">
	import Swiper from 'swiper'

	export default {
		props: {
			freeMode: {
				type: Boolean,
				default: true
			},
			data: {
				type: Array,
				default: null
			},
			autoplay: {
				type: Boolean,
				default: false
			},
			loop: {
				type: Boolean,
				default: false
			},
			direction: {
				type: String,
				default: null
			},
			observer: {
				type: Boolean,
				default: false
			},
		},
		created() {
			this.$options.methods._initScroll()
		},
		methods: {
			_initScroll() {
				setTimeout(() => {
					var mySwiper = new Swiper ('.swiper-container', {
					    freeMode: true,
					    observer: true,
  					})
				}, 20)      
			}
		},
		watch: {
			data() {
				setTimeout(() => {
					this.$options.methods._initScroll()
				}, 20)
			}
		}
	}
</script>