<template>
<div id="swiper-container">
    <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="item in list[0]">
        	<div class="mdui-ripple banner">
        		<img :src="item.pic">
        	</div>
        </div>
    </div>
</div>
</template>
<script type="text/javascript">
	import {slide} from '@/common/js/axios.js'
	import Swiper from 'swiper'

	export default {
		created() {
			this.$options.methods._banner(this.list)
		},
		data() {
			return {
				list: []
			}
		},
		methods: {
			_banner(list) {
				//let _this = this
				slide().then(function(res) {
					//这样才能触发Vue响应式机制
					list.push(res.data.banners)
				})
			},
			_refresh() {
				setTimeout(() => {
					var mySwiper = new Swiper ('#swiper-container', {
					    loop: true,
					    delay: 4000,
					    autoplay: true,
  					})
				}, 20)        
			}
		},
		watch: {
			list() {
				this.$options.methods._refresh()
			}
		}
	}
</script>

<style type="text/css" scoped>
	.banner {
		position: relative;
		width: 92%;
		margin-left: 4%;
		border-radius: 5px;
		overflow: hidden;
	}
	.banner img {
		position: relative;
		width: 100%;
		border-radius: 5px;
	}
</style>