class List {
	constructor({img, creator, name, id, tags, description, list, subscribedCount}) {
		this.img = img
		this.creator = creator
		this.name = name
		this.id = id
		this.tags = tags
		this.description =description
		this.list = list
		this.collect = subscribedCount
	}
}

export default function createList(playlist) {
	return new List({
		img: playlist.coverImgUrl,
		creator: playlist.creator,
		name: playlist.name,
		id: playlist.id,
		tags: playlist.tags,
		description: playlist.description,
		list: playlist.tracks,
		subscribedCount: playlist.subscribedCount
	})
}