import * as types from './mutation-types'

export const selectPlay = function({commit, state}, {song, index}) {
	/*commit(types.SET_SEQUENCE_LIST, list)
	commit(types.SET_PLAYLIST, list)*/
	commit(types.SET_CURRENT_INDEX, index)
	commit(types.SET_CURRENT_SONG, song)
	commit(types.SET_FULL_SCREEN, true)
	commit(types.SET_PLAYING_STATE, true)
}

export const togglePlayer = function({commit, state}) {
	let flag = null
	if (state.fullScreen === false) {
		flag = true
	} else {
		flag = false
	}
	commit(types.SET_FULL_SCREEN, flag)
}

export const togglePlay = function({commit, state}) {
	var flag = null
	if (state.playing) {
		flag = false
	} else {
		flag = true
	}
	commit(types.SET_PLAYING_STATE, flag)
}