<template>
<div>
	<div class="top">
		<div class="back mdui-ripple">
			<i class="mdui-icon material-icons">&#xe5c4;</i>
		</div>
			<div class="mdui-textfield mdui-textfield-floating-label input" @keyup.enter="search">
			  <input class="mdui-textfield-input" v-model="key">
			</div>
	</div>
	<ul class="mdui-list list">
	  <li class="mdui-list-item mdui-ripple" v-for="(item, index) in list[0]" @click="selectItem(item, index, list[0])">
	  	<div class="song-info">
	  			<p>{{item.name}}</p>
			<div class="info">
		  		<p>{{item.album.name}} - {{item.artists[0].name}}</p>
		  	</div>
	  	</div>
	  </li>
	</ul>
</div>
</template>

<script>
	import {searchMusic} from '@/common/js/axios.js'

	export default {
		props: {
			/**
			 * 搜索类型
			 * @type {
			 *       1: 主页搜索
			 * }
			 */
			type: {
				type: Number,
				default: 1
			}
		},
		data() {
			return {
				key: null,
				list: []
			}
		},
		methods: {
			search() {
				let _this = this
				this.list = []
				if (this.type === 1) {
					searchMusic(this.key)
					.then((res) => {
						_this.list.push(res.data.result.songs)
					})
					.catch((err) => {
						console.log(err)
					})
				}
			},
			selectItem(item, index, list) {
				const data = {
					item: item,
					index: index,
					list: list
				}
				this.$emit('selectitem', data)
			}
		},
	}
</script>

<style type="text/css" scoped>
	.song-info p{
		margin: 0;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	.info p {
		font-size: 10px;
	}
	.top {
		position: relative;
		top: 0;
		width: 92%;
		margin-left: 4%;
		display: flex;
		align-items: center;
	}
	.list {
		position: relative;
		width: 92%;
		margin-left: 4%;
	}
	.list li {
		padding: 0;
	}
	.back {
		display: inline-block;
		border-radius: 50%;
	}
	.input {
		position: relative;
		width: 80%;
		margin-left: 10px;
		display: inline-block;
	}
	.back i {
		opacity: .8;
		font-size: 28px;
	}
</style>